html {
    box-sizing: border-box;
    font-size: 100%;
}

*, *:before, *:after {
    box-sizing: inherit;   
}

* {
    padding: 0;
    margin: 0;
    text-decoration: none;
}


// helper utilites classess

.wrapper-inner {
    max-width: 1170px;
    margin: 0 auto;
}

.w-50 {
    width: 50%;
}

.pb-0 {
    padding-bottom: 0 !important;
}

.pb-1 {
    padding-bottom: 1em !important;
}

img {
    max-width: 100%;
    height: auto;
}

.btn {
    position: relative;
    z-index: 5;
    font-family: futurastd-light,sans-serif;
    color: #fff;
    border: 2px solid $lBlue;
    letter-spacing: 1px;
    background-color: transparent;
    padding: 1.2em 5.6em;
    font-size: .8rem;
    cursor: pointer;
    overflow: hidden;
}

.heading-h2 {
    position: relative;
    z-index: 5;
    font-family: 'futura-condensedbold';
    color: $h1-color;
    text-transform: uppercase;
    font-size: 2.9rem;
}

.heading-h2-white {
    color: $bgColor;
}

.rounded {
    border-radius: 100%;
    width: 180px;
    height: 180px;
}